
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
Tabbar,
TabbarItem
},
data () {
return {
}
},
mounted () {

let self = this
let path = self.$route.path
if (path === '/') {
self.$store.state.tabItem = 0
} else if (path === '/checkLoan') {
self.$store.state.tabItem = 1
} else if (path === '/my') {
self.$store.state.tabItem = 2
} else if (path === '/more') {
self.$store.state.tabItem = 3
}
},
computed: {
toTab: {
set (value) {
this.$store.state.tabItem = value
},
get () {
return this.$store.state.tabItem
}
}
},
activated () {
},
methods: {
toPage (flag) {
switch (flag) {
case 0: this.$router.replace('/')
break
case 1: this.$router.replace('/checkLoan')
break
case 2: this.$router.replace('/my')
break
case 3: this.$router.replace('/more')
break
}
}
}
}



